.main-scl-nav {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 22px;
    position: sticky;
    top: 0;
    z-index: 2;
}
.scl-logo-main {
display: flex;
gap: 12px;
}

.scl-nav-mega-bar {
    width: 250px;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap:30px;
    padding-top: 90px;
    transition: all;
    transition-duration: .4s;
    left : 0;
    z-index: 4;
   }

   .scl-nav-mega-bar-off {
    display: block;
    width: 250px;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap:30px;
    padding-top: 90px;
    transition: all;
    transition-duration: .4s;
    left : -255px;
   }

   .scl-layer-nav {
    width: 60%;
    background-color: rgba(76, 76, 76, 0.413);
    height: 1px;
   }

