.footer {
    width: 100%;
    height: 300px;
    background-color: #2a2a2a;
    position: relative;
    color:rgba(241, 241, 241, 0.777);
    text-align: center;
    font-size: 14px;
    font-weight: 500;
}

.footer-main {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom:0;
    margin: auto;
    width: fit-content;
    height: fit-content;
}

