.input-transparent-ctn {
    width: fit-content;
    height: fit-content;
}
.input-transparent-width-height {
    height: fit-content;
    width: 260px;
}
.input-trans-label {
    font-size: 14px;
    font-weight:600;
    color:#383838;
    display: block;
    margin: 10px 0;
}
.input-transparent {
    width: 100%;
    height: 100%;
    border-radius: 2px;
    outline: solid 1px #47a6ffb7;
    background-color: transparent;
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
    color:#262626;
}

.dark-input-trans-label {
    font-size: 14px;
    font-weight:600;
    color:#efefef;
    display: block;
    margin: 10px 0;
}
.dark-input-transparent {
    width: 100%;
    height: 100%;
    border-radius: 2px;
    outline: solid 1px #47a6ffb7;
    background-color: transparent;
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
    color:#e9e9e9;

}
.input-transparent:focus{
    outline: solid 1px #ce5dff;
}
.dark-input-transparent:focus{
    outline: solid 1px #ce5dff;
}

/* bottom outlined  */
.input-bottom-outlined-ctn {
    width: fit-content;
    height: fit-content;    
}
.input-bo-label {
    font-size: 14px;
    font-weight:600;
    color:#383838;
    display: block;
    margin: 10px 0;
}
.input-buttom-outlined-width-height {
    height: fit-content;
    width: 260px;
}
.dark-input-bo-label {
    font-size: 14px;
    font-weight:600;
    color:#d9d9d9;
    display: block;
    margin: 10px 0;
}
.input-bottom-outlined {
    width: 100%;
    height: 100%;
    border-radius: 2px;
    border-bottom:1px solid #134f88;
    outline: inherit;
    background-color: transparent;
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
}
.input-transparent:focus{
    border-bottom: solid 1px #427ce8;
}
.dark-input-bottom-outlined {
    outline: inherit;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    border-bottom:1px solid #a9d5ff;
    background-color: transparent;
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
    color:#e9e9e9;
}
.dark-input-transparent:focus{
    border-bottom: solid 1px #5591ff;
}

/* filled  */
.input-filled-ctn {
    width: fit-content;
    height: fit-content;
}
.input-filled-width-height {
    height: fit-content;
    width: 260px;
}
.input-filled-label {
    font-size: 14px;
    font-weight:600;
    color:#383838;
    display: block;
    margin: 10px 0;
}
.dark-input-filled-label {
    font-size: 14px;
    font-weight:600;
    color:#e8e8e8;
    display: block;
    margin: 10px 0;
}
.input-filled {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    outline: solid #f6f7f7f4 1px;
    background-color: #f6f7f7f4;
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
}
.input-filled:focus{
    outline: solid #d57cff 1px;
}

.dark-input-filled {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    outline: inherit;
    background-color: #181818f4;
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
    color:#e9e9e9;
}
.dark-input-filled:focus{
    outline: solid #a512e9 1px;
}