.card-animated-colored-outlined {
    height: fit-content;
    width: fit-content;
    border-radius: 5px;
    background-color: #ededed;
    overflow: hidden;
}
.card-animated-colored-outlined:hover .card-colored-layer{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background-color: #f692ff;
}

.dark-card-animated-colored-outlined {
    height: fit-content;
    width: fit-content;
    border-radius: 5px;
    background-color: #3e3e3e;
    overflow: hidden;
}
.dark-card-animated-colored-outlined:hover .card-colored-layer{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background-color: #8b1695;
}

.card-colored-outlined-width-height {
    position: relative;
    width: 340px;
    height: 270px;
}
.card-colored-layer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: -101%;
    background-color: #b805c8;
}
.card-colored-outlined-body {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 10px 18px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 22px;
}
.card-title {
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    color:#222222
}
.card-desc {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color:#676767;
}

.dark-card-title {
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    color:#ececec
}
.dark-card-desc {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color:#b6b6b6;
}
/* Colored Button Css */
.button-animated-colored-ctn {
    width: fit-content;
    height: fit-content;
    overflow: hidden;
    border-radius: 4px;
    margin: 12px auto;
}
.button-position-width-height {
        width: 130px;
        height: 30px;
        position: relative;    
}
.button-layer-bg {
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    background-color: #e08421;
    left:-101% ;
}

.button-animated-colored-ctn:hover .button-layer-bg  {
        position: absolute;
        top:0;
        width: 100%;
        height: 100%;
        background-color: #e08421;
        left:0 ;
}
.button-animated-colored-ctn:hover .button-animated-colored{
    color: #dedddd;
}
.button-animated-colored {
    padding: 0px 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    background-color: transparent;
    border : solid 2px #e08421;
    color: #e08421;
    border-radius: 4px;
}

/* card image */

.card-image-animated-filter-effect {
    width: fit-content;
    height: fit-content;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    user-select: none;
}
.card-image-animated-filter-effect:hover .card-image-layer{
    height: 100%;
    width: 100%;
    position: absolute;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:10px;
    padding: 6px 20px;
    background-color: #00000098;
}
.card-image-animated-filter-effect:hover .card-img-title  {
    opacity: 1;
}
.card-image-animated-filter-effect:hover .card-img-desc  {
    opacity: 1;
}
.card-image-width-height {
    position: relative;
    width: 340px;
    height: 270px;
}
.card-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.card-image-layer {
    height: 100%;
    width: 100%;
    position: absolute;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:10px;
    padding: 6px 20px;
    background-color: #00000000;
}
.card-img-title {
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    opacity: 0;
    color:#f1f1f1
}
.card-img-desc {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    opacity: 0;
    color:#bdbdbd;
}
