/* Default Skeleton with custom width and height  */

.wh-260px-13px {
    width: 260px;
    height: 13px;
}

.skeleton-default-container {
    width: 100%;
    height: 100%;
    border-radius:10px;
}

/* Rounded Skeleton with custom width and height  */

.wh-55px {
    width: 55px;
    height: 55px;
}
.skeleton-rounded-container {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

/* Square Skeleton with custom width and height  */

.skeleton-square-container {
    width: 100%;
    height: 100%;
    border-radius: 4px;
}
.wh-260px-200px {
    width: 260px;
    height: 200px;
}
.skeleton-border-container {
    width: fit-content;
    height: fit-content;
    padding: 10px 12px;
    border-radius: 5px;
    border: solid 1px #a2a2a2;
}

/* Border Line Skeleton with custom width and height  */

.skeleton-border-animations {
    border:solid 1px #a2a2a2;
    animation-name: skeleton-default;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}
/* For Animation  */
.skeleton-animations {
    background-color: #a2a2a2;
    animation-name: skeleton-default;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

@keyframes skeleton-default {
  0% {opacity: 1;}
  50% {opacity: .6;}
  100% {opacity: 1;}
}

