
.avator-default-rounded-ctn {
    width: 100%;
    height: 100%;
}

.avator-default-rounded-image {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border:solid 1px #9a9a9a6d;
    object-fit: cover;
    object-position: center;
    position:relative;
}

.avator-default-rounded-image:hover {
    filter:brightness(.9)
}

.avator-default-square-ctn {
    width: 100%;
    height: 100%;
} 
.avator-default-square-image {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    border:solid 1px #9a9a9a6d;
    object-fit: cover;
    object-position: center;
    position:relative;
}
.avator-default-square-image:hover {
    filter:brightness(.9)
}

.avator-countergroup{
    display: flex;
    width: fit-content;
    height: fit-content;
    margin: 0 auto;
}
.avator-ctn{
    width: 60px;
    height: 60px;
    margin-left: -30px;
}

.avator-counter-ctn {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #323232;
    margin-left: -30px;
    cursor: pointer;
    user-select: none;
}
.avator-counter-ctn:hover{
filter: brightness(.9);
}
.avator-countergroup-image{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border:solid #9595958d 1px;
    object-fit: cover;
    object-position: center;
}
.avator-counter {
    color:#ebebeb;
    text-align: center;
    width: fit-content;
    height: fit-content;
    font-size: 14px;
    font-weight: 700;

}

/* avator-dropdown */

.avator-dropdown {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap:10px;
}
.avator-dropdown-icon {
    width: 50px;
    height: 50px;
}
.avator-dropdown-image{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    cursor: pointer;
}
.avator-dropdown-image:hover {
    filter:brightness(.8)
}

.avator-dropdown-body-active {
    width: 200px;
    height: fit-content;
    border-radius: 8px;
    filter: drop-shadow(0 20px 13px rgb(0 0 0 / 0.03)) drop-shadow(0 8px 5px rgb(0 0 0 / 0.08));
    background-image: linear-gradient(to left , #fafafa, #e4e7e9, #f1f1f1);
}
.avator-dropdown-body {
    width: 200px;
    position:relative;
    top:0;
    height:fit-content;
    transform: scale(0);
    border-radius: 8px;
    filter: drop-shadow(0 20px 13px rgb(0 0 0 / 0.03)) drop-shadow(0 8px 5px rgb(0 0 0 / 0.08));
    background-image: linear-gradient(to left , #fafafa, #e4e7e9, #f1f1f1);
}

.dark-avator-dropdown-body-active {
    width: 200px;
    height: fit-content;
    border-radius: 8px;
    filter: drop-shadow(0 20px 13px rgb(0 0 0 / 0.03)) drop-shadow(0 8px 5px rgb(0 0 0 / 0.08));
    background-image: linear-gradient(to left , #3e3e3e, #2f3132, #3c3c3c);
}

.dark-avator-dropdown-body {
    width: 200px;
    position:relative;
    top:0;
    height:fit-content;
    transform: scale(0);
    border-radius: 8px;
    filter: drop-shadow(0 20px 13px rgb(0 0 0 / 0.03)) drop-shadow(0 8px 5px rgb(0 0 0 / 0.08));
    background-image: linear-gradient(to left , #3e3e3e, #2f3132, #3c3c3c);
}
.avator-first-ctn {
    padding: 12px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:6px;
}
.avator-name {
    font-size: 16px;
    font-weight: 600;
    color: #323232;
}
.avator-email {
    font-size: 13px;
    font-weight: 500;
    color: #464646bc;
}
.avator-br {
    width: 100%;
    height: 1px;
    background-color: #969696c8;
    margin-top: 10px;
}
.avator-second-ctn {
    margin-top: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:5px;
}
.avator-nav {
    font-size: 14px;
    font-weight: 500;
    color:#262626;
    cursor: pointer;
    user-select: none;
    width: 90%;
    height: fit-content;
    padding: 10px 0;
    text-align: center;
}
.avator-nav:hover{
    color:rgb(80, 80, 255);
    background-color: #ffffff7e;

}
.avator-dropdown-btn-ctn {
    margin-top: 10px;
    width: 80%;
    height: fit-content;
}
.avator-dropdown-btn {
    padding: 8px 0 ;
    width:100%;
    height: fit-content;
    border-radius: 6px;
    background-color: #156ac6;
    font-size: 14px;
    font-weight: 500;
    color:#f1f1f1;
}
.avator-dropdown-btn:hover {
    background-color: #3a86d7;
}


.dark-avator-name {
    font-size: 16px;
    font-weight: 600;
    color: #ededed;
}
.dark-avator-email {
    font-size: 13px;
    font-weight: 500;
    color: #d4d4d4bc;
}
.dark-avator-br {
    width: 100%;
    height: 1px;
    background-color: #d3d3d3b8;
    margin-top: 10px;
}

.dark-avator-nav {
    font-size: 14px;
    font-weight: 500;
    color:#e8e8e8;
    cursor: pointer;
    user-select: none;
    width: 90%;
    height: fit-content;
    padding: 10px 0;
    text-align: center;
}
.dark-avator-nav:hover{
    color:rgb(134, 134, 255);
    background-color: #2c2c2c7e;
}
