.main-doc {
    padding: 0 20px;
    margin-top: 80px;
}
.int {
    padding-bottom: 12px;
    margin-bottom: 20px;
    border-bottom: solid 2px #FF703B;
    width: fit-content;
}
.int-title {
    font-size: 20px;
    font-weight: 700;
    color: #1a1a1a;
}
.int-desc {
    font-size: 17px;
    font-weight: 500;
    color: rgb(128, 128, 128);
    margin-top: 30px;
}
.int-desc2 {
    font-size: 17px;
    font-weight: 700;
    color: rgb(55, 55, 55);
    margin-top: 30px;
}

.int-layer {

    height: 1px;
    width: 100%;
    margin: 30px auto;
    background-color: rgb(99, 99, 99);
}
.quick-btn-ctn {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;

    justify-content: space-evenly;
    align-items: center;
}
.quick-start {
    width: 45%;
    height: 200px;
    padding: 10px 20px;
    border-radius: 5px;
    transition: all;
    transition-duration: .4s;
}
.quick-start:hover {
    background-color:#FF703B;
}
.quick-start:hover .qs-1  {
    color:rgb(247, 247, 247);
}
.quick-start:hover  .qs-2 {
    color:rgb(247, 247, 247);
}
.author-div {
    display: flex;
    gap: 20px;
    margin-top: 120px;
}


@media (max-width:700px) {
    .quick-start {
        
    width: 45%;
    height: 300px;
    padding: 10px 20px;
    border-radius: 5px;
    transition: all;
    transition-duration: .4s;

    }
}
@media (max-width:600px) {
    .quick-btn-ctn {
        gap:20px;
    }
    .quick-start {
        
    width: 90%;
    height: 200px;
    padding: 10px 20px;
    border-radius: 5px;
    transition: all;
    transition-duration: .4s;

    }
}
@media (max-width:380px) {
    .author-div {
        display: flex;
        flex-direction: column;
        gap: 0px;
        margin-top: 120px;
    }
}