body {
    margin: 0 ;
    padding: 0;
    background-color: #f4edda;
}

.title-f {
    font-family: 'Ysabeau Office', sans-serif;
}
.content-f {
    font-family: 'Ysabeau SC', sans-serif;
}
.asset-f {
    font-family: 'Pacifico', cursive;
}
/* color-main 1 */
.bg-main-w {
    background-color: #f4edda;
}

/* color-sec 2 */
.bg-sec-w {
background-color: #d5bca7;
}

/* dark-colors */
bg-main-d {
    background-color: #55371b;
}
.bg-sec-d {
    background-color: #f4f8f9;
}

.main-w {
    color: #2a2a2a;
}

.sec-w {
color: #422102;
}

.main-d {
    color: #ebebeb;
}
.sec-d {
    color: #dfc4ab;
}

/* assit-color */
.asset {
    color:#FF703B;
}
.btn-color {
    background-color: #FF703B;
}

.html-title{
    padding: 10px 20px;
    border-radius: 4px;
    color: #2a2a2a;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Ysabeau SC', sans-serif;
    background-color: rgb(255, 201, 136);
    width: fit-content;
    height: fit-content;
    margin-bottom: 20px;
}