.comp-nav-ctn-main {
    position: fixed;
    right: 35px;
    top:80px;
    height: 300px;
    width: fit-content;
    z-index: 3;
}
.comp-nav-ctn {
    display: flex;
    gap:10px;
    justify-content: flex-end;
    height: fit-content;
    align-items: center;
}
.comp-txt-act {
    background-image: linear-gradient(to right ,#fd8c63,#e7542b,#ff5218);
    width:200px;
    height: fit-content;
    padding: 10px 0;
    border-radius: 4px;
    transition: all;
    transition-duration: .5s;
    text-align: center;
    color:white;
}
.comp-txt {
    color:white;
    background-image: linear-gradient(to right ,#fd8c63,#e7542b,#ff5218);
    width: 0px;
    color:transparent;
    height: fit-content;
    padding:10px 0;
    border-radius: 4px;
    transition: all;
    transition-duration: .5s;
    transition-delay: .3s;
    text-align: center;

}
.comp-icn {
    padding: 10px;
    border-radius: 50%;
    height: fit-content;
    width: fit-content;
    color:black;
background-color: #fff9f8;
border:solid 2px #9e9e9e75;
transition: all;
transition-duration: .5s;
transform: rotate(180deg);
}

.comp-icn-act {
    padding: 10px;
    border-radius: 50%;
    height: fit-content;
    width: fit-content;
    color:black;
    background-color: #fff9f8;
    border:solid 2px #9e9e9e75;
    transition: all;
transition-duration: .5s;
transform: rotate(0deg);
}
/* .comp-icn:active{
    transform: rotate(360deg);
} */
.comp-nav-div-act {
    width: 80%;
    height: 300px;
    background-color: #fffdfd;
    margin-top: 3px;
    transition: all;
    transition-duration: .5s;
    transition-delay: .3s;
    overflow:auto;
}
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 1px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 110, 26, 0.922); 
  border-radius: 1px;
}

.comp-nav-div {
    width: 80%;
    height: 0px;
    color:transparent;
    background-color: #fffdfd;
    margin-top: 3px;
    transition: all;
    transition-duration: .5s;
    overflow:auto;
}
.comp-item-ctn {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:8px;
    color:rgb(69, 69, 69);
    height: fit-content;
}