.loader-mixed-color-width-height {
    width: 120px;
    height: 120px;
    position: relative;
}
.loader-mc {
    width: 100%;
    height: 100%;
    background-color: #f5fdfd;
    border-bottom:solid 3px purple;
    border-left:solid 3px purple;
    border-top: solid 3px #0086d3;
    border-right: solid 3px #0086d3;
    animation: loader-mc;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    border-radius: 50%;
    filter: drop-shadow(0 0px 20px #ffad6a);
}

.dark-loader-mc {
    width: 100%;
    height: 100%;
    background-color: #312828;
    border-bottom:solid 3px purple;
    border-left:solid 3px purple;
    border-top: solid 3px #0086d3;
    border-right: solid 3px #0086d3;
    animation: loader-mc;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    border-radius: 50%;
    filter: drop-shadow(0 0px 20px #ffad6a);
}
@keyframes loader-mc {
   0% {transform: rotate(0deg);}
   100% {transform: rotate(360deg)}

}

.loader-mc-txt  {
    position: absolute;
    z-index: 2;
    width: fit-content;
    height: fit-content;
    top:0; left: 0; bottom: 0; right: 0;
    margin :auto;
    color:#222222;
    font-size: 14px;
    font-weight: 400;
}
.dark-loader-mc-txt  {
    position: absolute;
    z-index: 2;
    width: fit-content;
    height: fit-content;
    top:0; left: 0; bottom: 0; right: 0;
    margin :auto;
    color:#e4e4e4;
    font-size: 14px;
    font-weight: 400;
}

/* button filled loader  */
.loader-button-filled-ctn {
    width: fit-content;
    height: fit-content;
}
.loader-button-filled-width-height {
    width: 130px;
    height: 36px;
    position: relative;
}
.loader-bf {
    background-color: #2c7ce5;
    cursor: pointer;
    user-select: none;
    border-radius: 4px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:10px;
    width: 100%;
    height: 100%;
}
.loader-bf:hover {
    background-color: #204fbd;
}
.bf-txt {
    color:#e4e4e4;
    font-size: 14px;
    font-weight: 500;
}
.bf-loader {
    border:solid 2px #f4f4f47a;
    border-top:solid 2px #f4f4f4;
    border-right:solid 2px #f4f4f4;
    background-color: transparent;
    width: 18px;
    height: 18px;
    border-radius: 50%;
}


/* button outlined loader  */
.loader-button-outlined-ctn {
    width: fit-content;
    height: fit-content;
}
.loader-button-outlined-width-height {
    width: 130px;
    height: 36px;
    position: relative;
}
.loader-bo {
    background-color: transparent;
    border:#7a0c9b solid 2px;
    cursor: pointer;
    user-select: none;
    border-radius: 4px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:10px;
    width: 100%;
    height: 100%;
}
.loader-bo:hover {
    border:#62117b solid 2px;
}
.bo-txt {
    color:#7a0c9b;
    font-size: 14px;
    font-weight: 500;
}
.bo-loader {
    border:solid 2px #9f9f9f11;
    border-top:solid 2px #62117b;
    border-left:solid 2px #62117b;
    border-right:solid 2px #62117b;
    background-color: transparent;
    width: 18px;
    height: 18px;
    border-radius: 50%;
}

.btn-loader-animate {
    animation-name: b-loader-animate;
    animation-iteration-count: infinite;
    animation-duration: 2s;
}
@keyframes b-loader-animate {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}