.ac-ctn {
    width: 80%;
    margin: 90px auto;
    height: fit-content;
    border-radius: 8px;
    background-color:#dcdcdc;
    position: relative;
    padding:50px;
    transition: all;
    transition-duration: .5s;
    transition-delay: .2s;
}

.ac-ctn-dark {
    width: 80%;
    margin: 90px auto;
    height: fit-content;
    border-radius: 8px;
    background-color: #626262;
    position: relative;
    padding:50px;
    transition: all;
    transition-duration: .5s;
    transition-delay: .2s;
}
.dark-mode {
    position: absolute;
    top:10px;
    right: 10px;
    padding: 10px;
    border-radius: 50%;
    cursor: pointer;
    user-select: none;
    font-size: 20px;
    transition: all;
    transition-duration: .4s;
}
.dark-mode:hover{
    color: #e75b34;
}
.default-accordion{
    cursor: pointer;
    user-select: none;
    position: relative;
    width: 100%;
    height: fit-content;
    background-color: #fafafa;
    color:rgb(47, 47, 47);
    font-size: 15px;
    font-weight: 600;
    padding: 20px 16px;
    border-radius: 4px;
    filter: drop-shadow(0 10px 8px rgb(0 0 0 / 0.04)) drop-shadow(0 4px 3px rgb(0 0 0 / 0.1));
}
.dark-default-accordion{
    cursor: pointer;
    user-select: none;
    position: relative;
    width: 100%;
    height: fit-content;
    background-color: #444444;
    color:rgb(188, 188, 188);
    font-size: 15px;
    font-weight: 600;
    padding: 20px 16px;
    border-radius: 4px;
    filter: drop-shadow(0 10px 8px rgb(0 0 0 / 0.04)) drop-shadow(0 4px 3px rgb(0 0 0 / 0.1));
}


.transitions {
    transition: all;
    transition-timing-function:ease-in-out;
    transition-duration: .4s;
}
.accordion-icon-ctn {
    position: absolute;
    right: 20px;
    top:0;
    bottom:0;
    margin: auto;
    width: fit-content;
    height: fit-content;
}
.accordion-icon {
    font-size: 18px;
    transform: rotate(180deg);
}

.accordion-icon-active {
    font-size: 18px;
}

.default-accordion-body-active{
    padding: 24px 18px;
    font-size: 14px;
    font-weight: 500;
    color:#0000009b;
    width:100%;
    height: fit-content;
    background-color: #f3f3f3;
}
.default-accordion-body{
    padding: 0px 18px;
    font-size: 14px;
    font-weight: 500;
    color:transparent;
    width:100%;
    height: 0px;
    background-color: #f3f3f3;
    user-select: none;
}

.dark-default-accordion-body-active{
    padding: 24px 18px;
    font-size: 14px;
    font-weight: 500;
    color:#dfdfdfcc;
    width:100%;
    height: fit-content;
    background-color: #565656;
}
.dark-default-accordion-body{
    padding: 0px 18px;
    font-size: 14px;
    font-weight: 500;
    color:transparent;
    width:100%;
    height: 0px;
    background-color: #565656;
    user-select: none;
}