.intro-div {
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    padding: 0 90px;
    height: 100vh;
}
.title-div-ctn {
    width: 40%;
}
.logo-img {
    width: 400px;
    height: 400px;
    object-fit: cover;
    object-position: center;
    border-radius: 9px;
    border: 2px solid rgba(184, 184, 184, 0.321);
}
.title-btn {
    border : #FF703B solid 1px;
    width: fit-content;
    padding: 8px 26px;
    cursor: pointer;
    user-select: none;
}

.des-div{
    margin-top: 90px;
}
.html-div {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 60px;
}
.div-1 {
width: 30%;
padding-top: 20px;
height: 100%;
border-radius: 4px;
}
.ui-ctn {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 20px;
    width: 100%;
    flex-wrap: wrap;
}
.uis {
    width: 37%;
    height: 50px;
    background-color: #f9f9f9;
    font-size: 15px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 0 20px;
    position: relative;
    overflow: hidden;
    transition: all;
    transition-duration: .4s;
}
.uis-name {
    color:rgb(65, 65, 65);
}
.uis-layer {
    color:#c1c1c198;
    font-size: 40px;
    position: absolute;
    bottom: -20px;
    right: 100px;
}
.uis-layer-2 {
    color:#c1c1c198;
    font-size: 40px;
    position: absolute;
    top: -20px;
    right: 30px;
    transform: rotate(40deg);
}
.uis-layer-3 {
    color:#c1c1c198;
    font-size: 40px;
    position: absolute;
    top: -20px;
    right: 200px;
}


