.main-dev {
    padding: 0 23px;
    margin-top: 80px;
}
.dev-form-ctn {
    width: 60%;
    margin: 40px auto;
    height: 650px;
    position: relative;
    overflow: hidden;
    border: solid 2px rgba(166, 166, 166, 0.416);
}
.dev-form-layer{
    position: absolute;
    /* top:-200px; */
width: 180%;
height: 160%;
object-fit: cover;
object-position:center;    
}
.dev-form-layer2 {
    width:100%;
    height:100%;
    background-color: #fffbfbe2;
    position: absolute;
    top:0;
}
.form-layer {
    width: 60%;
    left:0;
    right: 0;
    top:30px;
    margin:  auto;
    position: absolute;
}
.email-fm {
    width: 100%;
}
.e-i {
    width: 100%;
    padding:8px 15px;
    background-color: inherit;
    border:solid #e0511da7 2px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 600;
    color:rgb(41, 41, 41)
}
.m-i {
    width: 100%;
    padding:8px 15px;
    background-color: inherit;
    border:solid #e0511d9b 2px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 600;
    color:rgb(41, 41, 41)
}
.m-i:focus{
    outline :  solid #e0511d 1px;
}
.e-i:focus{
    outline :  solid #e0511d 1px;
}
.fl-btn-ctn {
    width: 230px;
    height: fit-content;
    margin: 23px auto;   
}
.fl-btn {
    padding: 10px 0;
    height: 100%;
    width: 100%;
    color:white;
}


@media (max-width:700px) {
    
    .dev-form-ctn {
        width: 80%;
        margin: 40px auto;
        height: 650px;
        position: relative;
        overflow: hidden;
    }


}

@media (max-width:550px) {
    
    .dev-form-ctn {
        width: 85%;
        margin: 40px auto;
        height: 650px;
        position: relative;
        overflow: hidden;
    }
    .form-layer {
        width: 80%;
        left:0;
        right: 0;
        top:30px;
        margin:  auto;
        position: absolute;
    }
    .fl-btn-ctn {
        width: 100%;
        height: fit-content;
        margin: 23px auto;   
    }
}