/* Default Button Css */

.button-default-ctn {
    width: fit-content;
    height: fit-content;
}
.button-width-height {
    width: 130px;
    height: fit-content;
}

.button-default {
    padding: 8px 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    color:rgb(243, 243, 243);
    background-color: #0f67ff;
    border-radius: 6px;
}
.button-default:hover {
    background-color: #0b4dc8;
}

/* Outlined Button Css */
.button-animated-outline-ctn{
    width: fit-content;
    height: fit-content;
}
.button-animated-outline {
    padding: 8px 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    color:rgb(243, 243, 243);
    background-color: #de0000;
    border-radius: 6px;
    border: solid 2px #de0000;
}
.button-animated-outline:hover {
    color:#f40606;
    background-color: transparent;
}

/* Filled Button Css */
.button-animated-filled-ctn{
    width: fit-content;
    height: fit-content;
}
.button-animated-filled {
    padding: 8px 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    color:#e707e7;
    background-color: transparent;
    border-radius: 6px;
    border: solid 2px #e707e7;
}
.button-animated-filled:hover {
    color:white;
    background-color: #e707e7;
}

/* Colored Button Css */
.button-animated-colored-ctn {
    width: fit-content;
    height: fit-content;
    overflow: hidden;
    border-radius: 4px;
}
.button-position-width-height {
        width: 130px;
        height: 40px;
        position: relative;    
}
.button-layer-bg {
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    background-color: #e08421;
    left:-101% ;
}

.button-animated-colored-ctn:hover .button-layer-bg  {
        position: absolute;
        top:0;
        width: 100%;
        height: 100%;
        background-color: #e08421;
        left:0 ;
}
.button-animated-colored-ctn:hover .button-animated-colored{
    color: #dedddd;
}
.button-animated-colored {
    position: absolute;
    padding: 8px 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    background-color: transparent;
    border : solid 2px #e08421;
    color: #e08421;
    border-radius: 6px;
    border-radius: 4px;
}


/* Colored Button Css But Reverse Style */
.button-animated-colored-reverse-ctn {
    width: fit-content;
    height: fit-content;
    overflow: hidden;
    border-radius: 4px;
}
.button-reverse-position-width-height {
        width: 130px;
        height: 40px;
        position: relative;    
}

.button-reverse-layer-bg {
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    background-color: #02c13b;
    left:0 ;
}

.button-animated-colored-reverse-ctn:hover .button-reverse-layer-bg  {
        position: absolute;
        top:0;
        width: 100%;
        height: 100%;
        background-color: #02c13b;
        left:-101% ;
}
.button-animated-colored-reverse-ctn:hover .button-animated-colored-reverse{
    color: #02c13b;
}
.button-animated-colored-reverse {
    position: absolute;
    padding: 8px 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    background-color: transparent;
    border : solid 2px #02c13b;
    color: #f0f0f0;
    border-radius: 6px;
    border-radius: 4px;
}
