.main-nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding:  10px 30px;
    padding-right: 200px;
    align-items: center;
    position: absolute;
    top:0;
    z-index: 2;
}

.nav {
    font-size: 15px;
    font-size: 800;
}

.logo {
    font-size: 16px;
    font-weight: 500;
}

.items {
    display: flex;
    gap: 50px;
    align-items: center;
}
.register {
    padding: 8px 27px;
    font-size: 14px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    user-select: none;
}
.menu-bar {
    display: none;
}
.menu-bar-off {
    display: none;
}
.nav-mega-bar {
    display: none;
}
.nav-mega-bar-off {
    display: none;
}


@media (max-width:675px) {
   .menu-bar {
    display: block;
   } 
   .items {
    display: none;
   }
   .logo {
    font-size: 14px;
    font-weight: 500;
}
   .nav-mega-bar {
    display: block;
    width: 250px;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap:30px;
    padding-top: 90px;
    transition: all;
    transition-duration: .4s;
    left : 0;
   }
   .nav-mega-bar-off {
    display: block;
    width: 250px;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap:30px;
    padding-top: 90px;
    transition: all;
    transition-duration: .4s;
    left : -255px;
   }

   .layer-nav {
    width: 60%;
    background-color: rgba(119, 119, 119, 0.41);
    height: 1px;
   }
   .mega-menu-icn {
    position: absolute;
    top: 20px;
    left: 30px;
   }
}