.alerts-default {
    width: 100%;
    height: fit-content;
    padding: 22px 20px;
    border-radius: 5px;
    background-color: #ebebeb;
}

.alerts-default:hover {
    background-color: #f1f1f1;
}
.dark-alerts-default {
    width: 100%;
    height: fit-content;
    padding: 22px 20px;
    border-radius: 5px;
    background-color: #545353;
    color:rgb(227, 227, 227);
}

.dark-alerts-default:hover{
    background-color: #393939;
}

/* Success */
.alerts-success {
    width: 100%;
    height: fit-content;
    padding: 22px 20px;
    border-radius: 5px;
    background-color: #b5ffdf;
}

.alerts-success:hover {
    background-color: #abffdb;
}
.dark-alerts-success {
    width: 100%;
    height: fit-content;
    padding: 22px 20px;
    border-radius: 5px;
    background-color: #545353;
    color:#0af129;
}
.dark-alerts-success:hover {
    background-color: #393939;
    color:#04d420
}
/* Warn */
.alerts-warning {
    width: 100%;
    height: fit-content;
    padding: 22px 20px;
    border-radius: 5px;
    background-color: #ffd8d8;
}

.alerts-warning:hover {
    background-color: #ffcccc;
}
.dark-alerts-warning {
    width: 100%;
    height: fit-content;
    padding: 22px 20px;
    border-radius: 5px;
    background-color: #545353;
    color:rgb(230, 14, 14);
}
.dark-alerts-warning:hover {
    background-color: #393939;
    color:rgb(205, 5, 5);
}

.inner-alerts{
    font-size: 14px;
    font-weight: 500;
    color: rgb(40, 40, 40);
}
.dark-inner-alerts {
    font-size: 14px;
    font-weight: 500;
}